import axios from "axios"
let baseUrl ;
if (process.env.NODE_ENV == 'development') {
	baseUrl = '/';//代理地址
}else{
	baseUrl = 'https://wodepaopaohao.github.io/';//上线地址
}
//自定义axios实例名为service
const service = axios.create({
	baseURL: baseUrl, // api的base_url
})
export {baseUrl}
export default service


